.aboutContainer {
  max-width: 880px;
  width: 100%;
  padding: 50px 10px;
  margin: 0 auto;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.wallpaperContainer {
  margin-bottom: 40px;
  width: 100%;
  height: 350px;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.aboutContent {
  display: flex;
}

.aboutContentLeftBlock {
  margin-right: 50px;
  max-width: 455px;
  width: 100%;
  p {
    color: #767676;
  }
}

.aboutContentRightBlock {
  color: #767676;
  ul {
    padding: 0;
    display: flex;
    flex-direction: column;
    li {
      margin-bottom: 5px;
      a {
        color: #000;
        font-weight: bold;
        text-decoration: underline;
        &:hover {
          color: #17789e;
        }
      }
    }
  }
}

.nameGraph {
  margin-right: 10px;
  color: #000;
  font-weight: bold;
}